import Vue from 'vue';
import Router from 'vue-router';
const _import = require('./_import_' + process.env.NODE_ENV);
//

/*layout */
import Layout from '@/modules/layout/view/layout';
import refresh from '@/components/refresh.vue';


Vue.use(Router);

/**
 * icon: sidebar
 * hidden: 'hidden:true' sidebar
 * redirect: 'redirect:noredirect' levelbar 
 * noDropdown: 'noDropdown: true' 
 */


export const constantRouterMap = [
	{
		path: '/',
		component: _import('login/view/index'),
		// redirect: '/login',
		hidden: true
	},
	// { path: '/404', component: _import(''), hidden: true },
	// {
	// 	path: '/',
	// 	// component: Layout,
	// 	redirect: '/dashboard/dashboard',
	// 	hidden: true
	// },
	{
		path: '/dashboard',
		component: Layout,
		redirect: '/dashboard/dashboard',
		name: '',
		// hidden: true,
		noDropdown: true,
		icon: 'icon-shouye',
		children: [{
			path: 'dashboard',
			component: _import('dashboard/view/index')
		},
		{
			path: '/refresh',
			component: refresh,
			name: 'refresh'
		}
		]
	},
	{
		path: '/shop',
		component: Layout,
		redirect: '/shop/shopinfo',
		name: '',
		icon: 'icon-shop-fill',
		children: [{
			path: 'shopinfo',
			component: _import('shop/view/shopinfo/index'),
			name: ''
		},
		{
			path: 'staff',
			component: _import('shop/view/staff/index'),
			name: ''
		},
		{
			path: 'table',
			component: _import('table/view/index'),
			name: ''
		},
		{
			path: 'good',
			component: _import('merchandise/view/index'),
			name: ''
		},
		{
			path: 'customer',
			component: _import('customer/view/index'),
			name: ''
		},
		{
			path: 'printer',
			component: _import('printer/view/index'),
			name: ''
		},
		{
			path: 'silver',
			component: _import('silver/view/index'),
			name: ''
		},
		]
	},
	{
		path: '/statistical',
		component: Layout,
		redirect: '/statistical/orderform',
		name: '',
		icon: 'icon-yuyue',
		children: [{
			path: 'ordierform',
			component: _import('statisticalform/view/orderform'),
			name: ''
		},
		{
			path: 'advance',
			component: _import('statisticalform/view/advance'),
			name: ''
		},
		{
			path: 'bills',
			component: _import('statisticalform/view/bills'),
			name: ''
		},
		]
	},
	{
		path: '/message',
		component: Layout,
		redirect: '/message/systemmsg',
		name: '',
		icon: 'icon-xiaoxi',
		children: [{
			path: 'systemmsg',
			component: _import('messagecenter/view/systemmsg'),
			name: ''
		},
		{
			path: 'mass',
			component: _import('messagecenter/view/mass'),
			name: ''
		},
		]
	},
	{
		path: '/shopinfo',
		component: Layout,
		redirect: '/shopinfo/basicinformation',
		name: '',
		hidden: true,
		children: [{
			path: 'basicinformation',
			component: _import('shop/view/shopinfo/children/basicinformation'),
			name: ''
		},
		{
			path: 'commercialinformation',
			component: _import('shop/view/shopinfo/children/commercialinformation'),
			name: ''
		},
		{
			path: 'shopseting',
			component: _import('shop/view/shopinfo/children/shopseting'),
			name: ''
		}
		]
	},
	{
		path: '/good',
		component: Layout,
		redirect: '/good/foodeditor',
		name: '',
		hidden: true,
		children: [{
			path: 'foodeditor',
			component: _import('merchandise/view/foodEditor/index'),
			name: ''
		}, {
			path: 'accesseditor',
			component: _import('merchandise/view/accessEditor/index'),
			name: ''
		}, {
			path: 'drinkeditor',
			component: _import('merchandise/view/drinksEditor/index'),
			name: ''
		},{
			path: 'draft',
			component: _import('merchandise/view/draft/index'),
			name: ''
		}]
	},
	{
		path: '/table',
		component: Layout,
		redirect: '/table/edit',
		name: '',
		hidden: true,
		children: [
			{
				path: 'edit',
				component: _import('table/view/children/createNewTable'),
				name: ''
			},
			{
				path: 'menu',
				component: _import('table/view/children/menu'),
				name: ''
			}
		]
	}


];

export default new Router({
	// mode: 'history', //
	scrollBehavior: () => ({
		y: 0
	}),
	routes: constantRouterMap
});

